body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.main_div{
  width:100%;
  height:100vh;
  background:#6983aa;
  display:flex;
  flex-direction:row;
  justify-content:center;
  align-items:center;
  text-align:center;
}

.center_div{
  width:20%;
  height:80vh;
  background-color:#f4f4f4;
  box-shadow:5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius:15px;
}

h1{
  color:white;
  background:transparent;
  background-color:#8566aa;
  box-shadow:5px 5px 15px -5px rgba(0,0,0,0.3);
  padding:6px 0 2px 0;
  margin-bottom:10px;
}

input{
  text-align:center;
  height:30px;
  top:10px;
  border:none;
  background:transparent;
  font-size:20px;
 
  width:60%;
  border-bottom:2px solid #8566aa;
  outline:none;
}

button{
  min-height:40px;
  width:40px;
  border-radius:50%;
  border-color:transparent;
  background-color:#8566aa;
  color:#fff;
  font-size:30px;
  border:none;
  outline:none;
  margin-left:10px;
  box-shadow:5px 5px 15px -5px rgba(0,0,0,0.3);
}

button:hover{
  background-color:green;
}



#deletebutton{
  min-height:38px;
  width:38px;
  border-radius:50%;
  border-color:transparent;
  background-color:#8566aa;
  color:#fff;
  font-size:25px;
  border:none;
  outline:none;
  margin-left:5px;
  box-shadow:5px 5px 15px -5px rgba(0,0,0,0.3);

}
#deletebutton:hover{
  background-color:lightcoral;

}


@media (max-width:768px){
  .center_div{
    width:55%;
  }
}


button{
  margin-left:5px;
  min-height:40px;
  width:40px;
  color:#fff;
  font-size:30px;
}

li{
  margin-top:30px;
  padding-left:0px;
  text-align:left;
  font-size:20px;
  font-weight:500;
  min-height:40px;
  display:flex;
  align-items:center;
  color:#8566aa;
  text-transform:capitalize;
}
h2{
  
  margin-top:0px;
  text-align:center;
}